.backdrop {
  width: 100%;
  height: 100%;
  z-index: -100;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  background-color: #f5f5f5;
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(0%);
}

.is-open {
  z-index: 9;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  overflow: hidden;
  transform: scale(100%);
}

.modal {
  width: 280px;
  max-height: 100%;
  scrollbar-width: none;
  background-color: var(--light-text-color);
  color: var(--dark-text-color);
  border: 2px solid #f8d24e;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 30px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-x: hidden;
  overflow-y: auto;
  transform: translate(-50%, -50%);
}

.modal-close-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

